/* App.css */
.App-header {
  background-color: #282c34;
  min-height: 85vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
  text-align: center;
}

.image-container {
  float: right;
  margin-right: 100px;
}

.image-container img {
  width: 600px;
  height: auto;
  border-radius: 50%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
}

.text-container {
  text-align: left;
  margin-left: 50px;
  padding: 50px;
}

.text-container h1,
.text-container p {
  margin: 0;
  margin-bottom: 30px;
  font-weight: bold;
}

/* highlighted name */
.text-container .highlight {
  color: #ff0055;
  font-size: 1.2em;
  font-weight: bold;
}

/* style for the menu */
.App-menu {
  background-color: #333;
  padding: 20px;
  position: sticky;
  top: 0;
}

.App-menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
}

.App-menu li {
  margin: 0 10px;
}

.App-menu a {
  color: #fff;
  text-decoration: none;
  font-size: 16px;
  font-weight: bold;
  padding: 10px;
  transition: color 0.3s ease;
}

.App-menu a:hover {
  color: #61dafb;
}

/* Main section */
#about {
  background-color: #e9e9e9;
  padding: 50px;
}

#about h2 {
  font-size: 32px;
  margin-bottom: 20px;
  color: #333;
}

#about p {
  margin-bottom: 20px;
  color: #666;
}

#about a {
  color: #0066cc;
}

/* Projects section */
#projects {
  background-color: #f7f7f7;
  padding: 50px;
}

#projects h2 {
  font-size: 32px;
  margin-bottom: 20px;
  color: #333;
}

#projects h3 {
  font-size: 24px;
  margin-bottom: 10px;
  color: #333;
}

#projects ul {
  list-style-type: none;
  margin-bottom: 20px;
}

#projects li {
  margin-bottom: 10px;
  color: #666;
}

#projects a {
  color: #0066cc;
}

/* Skills section */
#skills {
  background-color: #e9e9e9;
  padding: 50px;
}

#skills h2 {
  font-size: 32px;
  margin-bottom: 20px;
  color: #333;
}

/* Ramblings section */
#ramblings {
  background-color: #f7f7f7;
  padding: 50px;
}

#ramblings .frh2 {
  font-size: 32px;
  margin-bottom: 20px;
  color: #333;
  text-align: center;
}

.blog-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.blog-card {
  min-width: 312px;
  background-color: #ffffff;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
  text-decoration: none;
  color: inherit;
  transition: transform 0.3s ease;
}

.blog-card:hover {
  transform: scale(1.02);
}

.featured-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.blog-info {
  padding: 20px;
}

.title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.author,
.date {
  font-size: 14px;
  color: #888888;
  margin-bottom: 5px;
}

.content {
  font-size: 16px;
  color: #333333;
}


/* Connect footer section */
#connect-footer {
  background-color: #e9e9e9;
  padding: 50px;
  text-align: center;
}

#connect-footer h2 {
  font-size: 32px;
  margin-bottom: 20px;
  color: #333;
}

#connect-footer p {
  margin-bottom: 20px;
  color: #666;
}

#connect-footer a {
  color: #0066cc;
}

@media (max-width: 912px) {
  .App-header {
    flex-direction: column;
  }

  .image-container {
    float: none;
    margin: 0 auto;
  }

  .image-container img {
    width: 280px;
    padding: 10px;
  }

  .text-container {
    text-align: center;
    margin: 20px auto;
  }

  .App-menu {
    display: none;
  }
}

@media (min-width: 912px) {
  .blog-card {
    width: 312px;
  }
}
